import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { selectAllSurveys } from '../store/root';
import { SURVEY_STATUS_KEY } from '../store/surveys';

export const useFilteredSurveysByStatus = (
  desiredStatus: SURVEY_STATUS_KEY
) => {
  const allSurveys = useSelector(selectAllSurveys);

  return useMemo(() => {
    return allSurveys.filter((survey) => survey.status === desiredStatus);
  }, [allSurveys, desiredStatus]);
};
