import * as React from 'react';
import { useIntl } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import { LOCALES, LOCALE_KEY } from '../i18n/locale';
import { selectSettings } from '../store/root';
import { settingsUpdateLocale } from '../store/settings';
import { useSelect } from './useSelect';

export const SettingsView: React.FunctionComponent = () => {
  const { formatMessage } = useIntl();
  const { locale: activeLocale } = useSelector(selectSettings);
  const dispatch = useDispatch();
  const {
    selectedValue: selectedLocale,
    selectField: localeSelect,
  } = useSelect({
    labelId: `locale`,
    options: LOCALES,
    initialValue: activeLocale,
  });

  React.useEffect(() => {
    dispatch(settingsUpdateLocale(selectedLocale as LOCALE_KEY));
  }, [dispatch, selectedLocale]);

  return (
    <React.Fragment>
      <h2>{formatMessage({ id: `settings` })}</h2>

      {localeSelect}
    </React.Fragment>
  );
};
