import * as React from 'react';
import { useIntl } from 'react-intl';
import { Link } from 'react-router-dom';

const useLinks = (): Array<{
  url: string;
  description: string;
}> => {
  const { formatMessage } = useIntl();

  return [
    {
      url: `/create-survey`,
      description: formatMessage({ id: `create-survey.header` }),
    },
    {
      url: `/surveys`,
      description: formatMessage({ id: `list-surveys.all` }),
    },
    {
      url: `/surveys/new`,
      description: formatMessage({ id: `list-surveys.new` }),
    },
    {
      url: `/surveys/in-progress`,
      description: formatMessage({ id: `list-surveys.in-progress` }),
    },
    {
      url: `/surveys/completed`,
      description: formatMessage({ id: `list-surveys.completed` }),
    },
    {
      url: `/settings`,
      description: formatMessage({ id: `settings` }),
    },
  ];
};

export const NavBar: React.FunctionComponent = () => {
  const links = useLinks();

  return (
    <nav>
      <ul>
        {links.map(({ url, description }, index) => (
          <li key={index}>
            <Link to={url} style={{ display: `inline-block`, padding: 8 }}>
              {description}
            </Link>
          </li>
        ))}
      </ul>
    </nav>
  );
};
