import { formatRFC7231, parseISO } from 'date-fns';
import * as React from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { Surveys, useSurveyStatusDescriptions } from '../store/surveys';

const TABLE_CELL_STYLES = {
  border: `solid 1px black`,
  padding: 8,
};

const TABLE_HEADER_MSG_IDS = [
  `list-surveys.title-header`,
  `list-surveys.creation-date-header`,
  `list-surveys.launch-date-header`,
  `list-surveys.status-header`,
];

const TableHeader: React.FunctionComponent = () => {
  const { formatMessage } = useIntl();

  return (
    <thead>
      <tr>
        {TABLE_HEADER_MSG_IDS.map((id, index) => (
          <th key={index} style={TABLE_CELL_STYLES}>
            {formatMessage({ id })}
          </th>
        ))}
      </tr>
    </thead>
  );
};

const TableBody: React.FunctionComponent<{ surveys: Surveys }> = ({
  surveys,
}) => {
  const surveyStatuses = useSurveyStatusDescriptions();
  const { formatMessage } = useIntl();

  return (
    <tbody>
      {surveys.map(({ _id, title, tsCreated, launchDate, status }) => {
        const tsCreatedAsString = formatRFC7231(parseISO(tsCreated));
        const launchDateAsString =
          launchDate === undefined
            ? formatMessage({ id: `em-dash` })
            : formatRFC7231(parseISO(launchDate));

        return (
          <tr key={_id}>
            <td style={TABLE_CELL_STYLES}>{title}</td>
            <td style={TABLE_CELL_STYLES}>{tsCreatedAsString}</td>
            <td style={TABLE_CELL_STYLES}>{launchDateAsString}</td>
            <td style={TABLE_CELL_STYLES}>{surveyStatuses[status]}</td>
          </tr>
        );
      })}
    </tbody>
  );
};

const TableBodyForNoData: React.FunctionComponent = () => {
  return (
    <tbody>
      <tr>
        <td colSpan={TABLE_HEADER_MSG_IDS.length} style={TABLE_CELL_STYLES}>
          <FormattedMessage id={`list-surveys.empty-table`} />
        </td>
      </tr>
    </tbody>
  );
};

export const SurveysList: React.FunctionComponent<{
  surveys: Surveys;
}> = ({ surveys }) => {
  return (
    <table style={{ border: `solid 1px black`, borderCollapse: `collapse` }}>
      <TableHeader />
      {surveys.length > 0 ? (
        <TableBody surveys={surveys} />
      ) : (
        <TableBodyForNoData />
      )}
    </table>
  );
};
