import { formatISO } from 'date-fns';
import * as React from 'react';
import { useIntl } from 'react-intl';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { surveysCreateOne } from '../store/surveys';
import { useInitialStatusSelect } from './useInitialStatusSelect';
import { useLaunchDateInput } from './useLaunchDateInput';
import { useTitleInput } from './useTitleInput';

export const CreateSurveyView: React.FunctionComponent = () => {
  const { formatMessage } = useIntl();
  const history = useHistory();
  const dispatch = useDispatch();
  const { titleValue, titleInput } = useTitleInput();
  const { launchDate, launchDateInput } = useLaunchDateInput();
  const { initialStatus, initialStatusSelect } = useInitialStatusSelect();
  const [showMissingTitleError, setShowMissingTitleError] = React.useState(
    false
  );

  React.useEffect(() => {
    setShowMissingTitleError(false);
  }, [titleValue]);

  const handleSubmit: React.FormEventHandler = (event) => {
    event.preventDefault();

    if (titleValue === undefined) {
      return setShowMissingTitleError(true);
    }

    dispatch(
      surveysCreateOne({
        title: titleValue,
        launchDate:
          launchDate === undefined ? undefined : formatISO(launchDate),
        status: initialStatus,
      })
    );
    history.push(`/surveys`);
  };

  return (
    <React.Fragment>
      <h2>{formatMessage({ id: `create-survey.header` })}</h2>

      <form onSubmit={handleSubmit}>
        {titleInput}
        {launchDateInput}
        {initialStatusSelect}

        <div style={{ marginTop: 32 }}>
          <button type={`submit`}>
            {formatMessage({ id: `create-survey.submit` })}
          </button>
        </div>

        {showMissingTitleError && (
          <div style={{ color: `red` }}>
            {formatMessage({ id: `create-survey.missing-title-error` })}
          </div>
        )}
      </form>
    </React.Fragment>
  );
};
