import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';
import { selectAllSurveys } from '../store/root';
import { SurveysList } from './SurveysList';

export const AllSurveys: React.FunctionComponent = () => {
  const allSurveys = useSelector(selectAllSurveys);

  return (
    <React.Fragment>
      <h2>
        <FormattedMessage id={`list-surveys.all`} />
      </h2>
      <SurveysList surveys={allSurveys} />
    </React.Fragment>
  );
};
