import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import { SURVEY_STATUS_KEY } from '../store/surveys';
import { SurveysList } from './SurveysList';
import { useFilteredSurveysByStatus } from './useFilteredSurveysByStatus';

export const StatusFilteredSurveys: React.FunctionComponent<{
  status: SURVEY_STATUS_KEY;
  headerMsgId: string;
}> = ({ status, headerMsgId }) => {
  const filteredSurveys = useFilteredSurveysByStatus(status);

  return (
    <React.Fragment>
      <h2>
        <FormattedMessage id={headerMsgId} />
      </h2>
      <SurveysList surveys={filteredSurveys} />
    </React.Fragment>
  );
};
