import { useDateInput } from '../base/useDateInput';

export const useLaunchDateInput = () => {
  const { inputValue: launchDate, inputField: launchDateInput } = useDateInput({
    labelId: `create-survey.launch-date-label`,
    isRequired: false,
    mode: `future-only`,
  });

  return { launchDate, launchDateInput };
};
