import { useSelect } from '../base/useSelect';
import { useSurveyStatusDescriptions } from '../store/surveys';

export const useInitialStatusSelect = () => {
  const surveyStatuses = useSurveyStatusDescriptions();

  const {
    selectedValue: initialStatus,
    selectField: initialStatusSelect,
  } = useSelect({
    labelId: `create-survey.status-label`,
    options: surveyStatuses,
    initialValue: `new`,
  });

  return {
    initialStatus,
    initialStatusSelect,
  };
};
