import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { HashRouter, Redirect, Route, Switch } from 'react-router-dom';
import { DebugView } from './base/DebugView';
import { NavBar } from './base/NavBar';
import { SettingsView } from './base/SettingsView';
import { CreateSurveyView } from './create-survey/CreateSurveyView';
import { IntlWrapper } from './i18n/IntlWrapper';
import { AllSurveys } from './list-surveys/AllSurveys';
import { StatusFilteredSurveys } from './list-surveys/StatusFilteredSurveys';
import * as serviceWorker from './serviceWorker';
import { store } from './store/root';

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <IntlWrapper>
        <HashRouter>
          <div style={{ fontFamily: `sans-serif` }}>
            <NavBar />
            <hr />

            <Switch>
              <Route exact path={`/settings`}>
                <SettingsView />
              </Route>

              <Route exact path={`/create-survey`}>
                <CreateSurveyView />
              </Route>

              <Route exact path={`/surveys`}>
                <AllSurveys />
              </Route>

              <Route exact path={`/surveys/new`}>
                <StatusFilteredSurveys
                  status={`new`}
                  headerMsgId={`list-surveys.new`}
                />
              </Route>
              <Route exact path={`/surveys/in-progress`}>
                <StatusFilteredSurveys
                  status={`in-progress`}
                  headerMsgId={`list-surveys.in-progress`}
                />
              </Route>
              <Route exact path={`/surveys/completed`}>
                <StatusFilteredSurveys
                  status={`completed`}
                  headerMsgId={`list-surveys.completed`}
                />
              </Route>

              <Route path="*">
                <Redirect to={`/surveys`} />
              </Route>
            </Switch>

            <hr />
            <DebugView />
          </div>
        </HashRouter>
      </IntlWrapper>
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
