import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { formatISO } from 'date-fns';
import { useIntl } from 'react-intl';
import { v4 as uuidv4 } from 'uuid';

export type SURVEY_STATUS_KEY = `new` | `in-progress` | `completed`;

interface Survey {
  _id: string;
  title: string;
  tsCreated: string;
  launchDate?: string;
  status: SURVEY_STATUS_KEY;
}

export type Surveys = Array<Survey>;

const initialState: Surveys = [];

const surveysSlice = createSlice({
  name: `surveys`,
  initialState,
  reducers: {
    surveysCreateOne: (
      state,
      action: PayloadAction<Omit<Survey, 'tsCreated' | '_id'>>
    ) => {
      return [
        ...state,
        {
          ...action.payload,
          _id: uuidv4(),
          tsCreated: formatISO(new Date()),
        },
      ];
    },
  },
});

export const { surveysCreateOne } = surveysSlice.actions;

export const { reducer } = surveysSlice;

export const useSurveyStatusDescriptions = (): Record<
  SURVEY_STATUS_KEY,
  string
> => {
  const { formatMessage } = useIntl();

  return {
    new: formatMessage({ id: `survey-status.new` }),
    'in-progress': formatMessage({ id: `survey-status.in-progress` }),
    completed: formatMessage({ id: `survey-status.completed` }),
  };
};
